import Vue from "vue";
import VueRouter from "vue-router";
import routers from "../routers/routers.config.js";
var router=new VueRouter({
     routes:routers
});

new Vue({
     el:".container",
     template:`
     <ul>
        <li><router-link :to="{name:'overall'}"></router-link></li>
        <li><router-link :to="{name:'college'}"></router-link></li>
        <li><router-link :to="{name:'major'}"></router-link></li>
    </ul>
    <router-view></router-view>`,
    router:router

})


